.container {
  max-width: 1366px;
  width: 100%;
  margin: 0 auto;
  padding: 0 112px; }

@media (max-width: 1024px) {
  .container {
    min-width: 320px; } }

/* ----------------- Разметкdа под desktop ----------------- */
.page-header__container {
  position: relative;
  margin-top: 32px; }

.page-header__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 48px;
  margin: 0;
  padding: 0;
  list-style: none; }

.page-header__link {
  color: #484848;
  font-weight: 500; }
  .page-header__link--order {
    text-transform: uppercase;
    border-bottom: 1px dashed #484848; }
  .page-header__link:hover, .page-header__link:focus {
    color: #ffc341;
    border-color: #ffc341;
    transition: 0.1s; }
  .page-header__link:active {
    color: #ffc341;
    opacity: 0.7; }

.page-main {
  position: relative; }
  .page-main::after {
    content: "";
    position: absolute;
    top: -80px;
    left: 0;
    right: 0;
    display: block;
    height: 906px;
    background-color: rgba(227, 227, 227, 0.5);
    background-image: url(../img/background-desktop-headline.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1; }

.page-main__about-program-section {
  position: relative; }
  .page-main__about-program-section::after {
    content: "";
    position: absolute;
    top: 125px;
    left: -115px;
    right: 0;
    display: block;
    height: 604px;
    width: 1366px;
    background-image: url(../img/background-desktop-about_program-watercolor.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1; }

.page-main__programs-section {
  position: relative;
  margin-top: 200px;
  margin-bottom: 240px; }
  .page-main__programs-section::after {
    content: "";
    position: absolute;
    top: -50px;
    right: -100px;
    display: block;
    height: 426px;
    width: 680px;
    background-image: url(../img/background-desktop-programs-watercolor.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1; }

@media (min-width: 1367px) {
  .page-main::after {
    top: 0;
    height: 826px; }
  .page-main__about-program-section {
    margin-top: 20px; } }

/* ----------------- Разметкdа под desktop ----------------- */
.headline-section {
  display: flex;
  justify-content: center;
  min-height: 826px; }

.headline-section__description {
  width: 350px;
  padding: 0;
  padding-top: 460px;
  margin: 0;
  text-align: center;
  font-size: 24px;
  line-height: 130%;
  font-weight: bold;
  color: #ffffff; }

.about-program-section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-image: url(../img/background-desktop-about_program.jpg);
  background-position: 50% 100%;
  background-size: auto;
  background-repeat: no-repeat; }

.about-program-section__heading {
  margin: 0;
  padding: 0;
  font-size: 40px;
  line-height: 130%;
  text-transform: uppercase;
  font-weight: bold;
  color: #1fc0fc; }

.about-program-section__description {
  max-width: 720px;
  margin: 0 auto;
  margin-top: 20px;
  padding: 0;
  text-align: center; }

.about-program-section__link {
  position: relative;
  display: block;
  width: 64px;
  height: 64px;
  margin-top: 225px;
  margin-bottom: 185px;
  border-radius: 50%;
  background: #ffffff; }
  .about-program-section__link::after {
    position: absolute;
    top: 50%;
    left: 50%;
    content: "";
    width: 96px;
    height: 96px;
    background: #ffffff;
    border-radius: 50%;
    opacity: 0.32;
    transform: translate(-50%, -50%); }
  .about-program-section__link::before {
    position: absolute;
    top: 50%;
    left: 50%;
    content: "";
    display: block;
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-left: 20px solid #1fc0fc;
    border-bottom: 10px solid transparent;
    z-index: 1;
    transform: translate(-35%, -50%); }
  .about-program-section__link:hover, .about-program-section__link:focus {
    background-color: rgba(244, 244, 244, 0.9);
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.05); }
  .about-program-section__link:active {
    background-color: rgba(244, 244, 244, 0.32);
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.05); }

.programs-section__container {
  display: flex;
  justify-content: space-between; }

.programs-section__heading {
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 40px;
  line-height: 130%;
  text-transform: uppercase;
  font-weight: bold;
  color: #1fc0fc;
  margin-bottom: 80px; }

.programs-section__nav-list {
  margin: 0;
  padding: 0;
  list-style: none; }

.programs-section__item {
  position: relative;
  display: flex;
  align-items: center;
  width: 228px;
  margin-bottom: 36px;
  padding-left: 20px; }
  .programs-section__item:last-child {
    margin-bottom: 0; }
  .programs-section__item::before {
    position: absolute;
    top: -50%;
    right: -68px;
    content: "";
    display: block;
    width: 0;
    height: 0;
    border: 34px solid transparent;
    z-index: 1; }
  .programs-section__item::after {
    position: absolute;
    top: -50%;
    left: 0;
    content: "";
    display: block;
    width: 100%;
    height: 68px;
    z-index: -1; }

.programs-section__nav-heading {
  margin-left: 16px; }

.programs-section__img {
  fill: red; }

.programs-section__slider--general:checked ~ .programs-section__nav-list .programs-section__item--general::before,
.programs-section__slider--academic:checked ~ .programs-section__nav-list .programs-section__item--academic::before,
.programs-section__slider--internship:checked ~ .programs-section__nav-list .programs-section__item--internship::before,
.programs-section__slider--volunteering:checked ~ .programs-section__nav-list .programs-section__item--volunteering::before,
.programs-section__slider--religious:checked ~ .programs-section__nav-list .programs-section__item--religious::before {
  border-left: 34px solid #ffd701; }

.programs-section__slider--general:checked ~ .programs-section__nav-list .programs-section__item--general::after,
.programs-section__slider--academic:checked ~ .programs-section__nav-list .programs-section__item--academic::after,
.programs-section__slider--internship:checked ~ .programs-section__nav-list .programs-section__item--internship::after,
.programs-section__slider--volunteering:checked ~ .programs-section__nav-list .programs-section__item--volunteering::after,
.programs-section__slider--religious:checked ~ .programs-section__nav-list .programs-section__item--religious::after {
  background: linear-gradient(to right, #ffc341, #ffd701); }

.programs-section__slider--general:checked ~ .programs-section__nav-list .programs-section__nav-heading--general,
.programs-section__slider--academic:checked ~ .programs-section__nav-list .programs-section__nav-heading--academic,
.programs-section__slider--internship:checked ~ .programs-section__nav-list .programs-section__nav-heading--internship,
.programs-section__slider--volunteering:checked ~ .programs-section__nav-list .programs-section__nav-heading--volunteering,
.programs-section__slider--religious:checked ~ .programs-section__nav-list .programs-section__nav-heading--religious {
  font-size: 17px;
  text-transform: uppercase;
  font-weight: 500;
  color: #ffffff; }

.programs-section__slide-list {
  margin: 0;
  margin-left: 100px;
  margin-right: auto;
  padding: 0;
  list-style: none; }

.programs-section__slide {
  display: none;
  max-width: 560px; }

.programs-section__slide-head {
  margin: 0;
  padding: 0;
  font-size: 40px;
  line-height: 130%;
  text-transform: uppercase;
  font-weight: bold;
  color: #1fc0fc;
  font-size: 28px;
  text-transform: none; }

.programs-section__slider--general:checked ~ .programs-section__slide-list .programs-section__slide--general,
.programs-section__slider--academic:checked ~ .programs-section__slide-list .programs-section__slide--academic,
.programs-section__slider--internship:checked ~ .programs-section__slide-list .programs-section__slide--internship,
.programs-section__slider--volunteering:checked ~ .programs-section__slide-list .programs-section__slide--volunteering,
.programs-section__slider--religious:checked ~ .programs-section__slide-list .programs-section__slide--religious {
  display: block; }

.conditions-section__heading {
  margin: 0;
  padding: 0;
  font-size: 40px;
  line-height: 130%;
  text-transform: uppercase;
  font-weight: bold;
  color: #1fc0fc; }
